import {
  mapGetters,
  mapMutations
} from "vuex";
export default {
  name: "app",
  mounted() {
    setInterval(() => {
      this.setWeather();
    }, 60000);
  },
  methods: {
    ...mapMutations({
      setWeather: "SET_WEATHER"
    })
  }
};




